
import Search from 'vux/src/components/search/index.vue'


export default{
name:'info_home',
data () {
function getResult (val) {
let rs = []
for (let i = 0; i < 20; i++) {
rs.push({
title: `${val} result: ${i + 1} `,
other: i
})
}
return rs
}
return{
results: [],
value: 'test'
}
},
methods:{
onSubmit () {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},
onFocus () {
console.log('on focus')
},
onCancel () {
console.log('on cancel')
}
},
components:{
Search
}

}



